@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.test {
 font-family: Muli-Bold;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.wikipedia-search-engine-item{padding:10px 0;border-bottom:2px solid #00C2E8;display:flex;flex-direction:column;align-items:center;width:80%;max-width:1024px}

.ball-loader{width:100px;height:23.33333px;position:absolute;top:50%;left:50%;-webkit-transform:translateX(-50%) translateY(-50%);transform:translateX(-50%) translateY(-50%)}.ball-loader-ball{will-change:transform;height:23.33333px;width:23.33333px;border-radius:50%;background-color:#00C2E8;position:absolute;-webkit-animation:grow 1s ease-in-out infinite alternate;animation:grow 1s ease-in-out infinite alternate}.ball-loader-ball.ball1{left:0;-webkit-transform-origin:100% 50%;transform-origin:100% 50%}.ball-loader-ball.ball2{left:50%;-webkit-transform:translateX(-50%) scale(1);transform:translateX(-50%) scale(1);-webkit-animation-delay:0.33s;animation-delay:0.33s}.ball-loader-ball.ball3{right:0;-webkit-animation-delay:0.66s;animation-delay:0.66s}@-webkit-keyframes grow{to{-webkit-transform:translateX(-50%) scale(0);transform:translateX(-50%) scale(0)}}@keyframes grow{to{-webkit-transform:translateX(-50%) scale(0);transform:translateX(-50%) scale(0)}}

